import ApiService from "@/core/services/ApiService";
import JwtService from "@/core/services/JwtService";
import axios from 'axios';
import {httpOptionsFile, httpOptions} from '../../global';

const apiServie = new ApiService();

export default class WinnerService{
  getData(lotteryId: any, search: any) {
    return axios.get(`${ApiService.vueInstance.axios.defaults.baseURL}/lottery/${lotteryId}/winner?q=${search}`, httpOptions());
  }

  destroy(id:any) {
    return axios.delete(`${ApiService.vueInstance.axios.defaults.baseURL}/lottery/winner/${id}`, httpOptions());
  }
}
