
import { defineComponent, onMounted, ref } from "vue";
import { useRouter, useRoute } from "vue-router";
import LotteryService from "@/store/modules/lottery/service/lottery.service";
import WinnerService from "@/store/modules/lottery/service/winner.service";
import ILottery from "@/store/modules/lottery/interface/lottery.interface";
import Swal from "sweetalert2/dist/sweetalert2.js";

export default defineComponent({
	name: "LotteriesDetail",
	components: {
	},
	setup() {
    	const router = useRouter();
    	const route = useRoute();

		const lotteryService = new LotteryService();
		const winnerService = new WinnerService();

		const lottery = ref<ILottery>({
			id: null,
			name: '',
			visible: false,
			duration: 0,
			position: '',
			completed: false,
			winners: [],
			site_id: null
		});

		const search = ref<String>('');

		const winner = ref<Object>({});

		const getData = () => lotteryService.edit(route.params.id)
		.then((response) => {
			response.data.visible = (response.data.visible == 1)
			response.data.completed = (response.data.completed == 1)
			response.data.winners = []
			lottery.value = response.data
			getWinners(lottery.value.id)
		})
		.catch((error) => {
			Swal.fire(
			'Oh no!',
			'Información no encontrada',
			'error'
			);
			router.push({ name: "lotteries" });
		});

		const getWinners = (lotteryId: any) => {
			lottery.value.winners = []
			winnerService.getData(lotteryId, search.value)
			.then((response) => {
				lottery.value.winners = response.data
			})
			.catch((error) => {
				
			})
		};

		const deleteData = (winnerId: any) => {
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea eliminar el registro seleccionado?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Eliminar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						winnerService.destroy(winnerId)
						.then((response) => {
							Swal.fire(
							'Confirmación!',
							'Información eliminada exitosamente',
							'success'
							).then(result => {
								getWinners(route.params.id)
							});
						})
						.catch((error) => {
							Swal.fire(
							'Oh no!',
							'No se completo la operación',
							'error'
							);
						});
					}
			})
			
		}

		const selectWinner = (winnerData) => {
			winner.value = winnerData
			winner.value['optionList'] = getList(winnerData)
		}

		const validateWinner = () => {
			let disabled = false;
			if(!winner.value['id'] || !winner.value['winner_option'] || winner.value['winner_option'] == '') disabled = true;
			return disabled
		}

		const getList = (winnerData: any) => {
			let list = [];
			if(winnerData.options != ''){
				list = winnerData.options.split(',');
			}
			return list;
		}

		const saveWinner = () => {
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea actualizar los datos?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Actualizar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						winner.value['winner'] = 1
						storeWinner()
					}
			})
		}

		const removeWinner = (winnerData) => {
			Swal.fire({
				title: 'Confirmación!',
				text: "¿Esta seguro que desea remover el registro de ganadores?",
				icon: 'question',
				showCancelButton: true,
				confirmButtonColor: '#3085d6',
				cancelButtonColor: '#d33',
				confirmButtonText: 'Si, Quitar',
				cancelButtonText: 'No, Cancelar',
				reverseButtons: true
				}).then((result) => {
					if (result.isConfirmed) {
						winner.value = winnerData
						winner.value['winner'] = 0
						winner.value['winner_option'] = null;
						storeWinner()
					}
			})
		}

		const storeWinner = ()=>{
			lotteryService.updateWinner(winner.value['id'], winner.value)
			.then((response) => {
				Swal.fire(
				'Confirmación!',
				'Información actualizada exitosamente',
				'success'
				).then(result => {
					if(document.getElementById('animation-close-button')){
						document.getElementById('animation-close-button')?.click()
					}
					getWinners(lottery.value.id)
				});
			})
			.catch((error) => {
				Swal.fire(
				'Oh no!',
				'No se actualizaron los datos',
				'error'
				);
				winner.value['winner'] = 1
			});
		}

		onMounted(() => {
			getData()
		});


		return {
            lottery,
			deleteData,
			winner,
			validateWinner,
			selectWinner,
			saveWinner,
			removeWinner,
			search,
			getWinners
        };
  	},
});
